import {
  ProForm,
  ProFormDependency,
  ProFormInstance,
  ProFormRadio,
  ProFormSwitch,
} from '@ant-design/pro-components';
import { CopilotType } from '@sinsa/schema';
import { Card, List, Space, Typography } from 'antd';
import { useModel } from '@modern-js/runtime/model';
import numeral from 'numeral';
import { produce } from 'immer';
import { useLocalStorageState, useRequest } from 'ahooks';
import { useMemo, useRef, useState } from 'react';
import { calculateAllScenariosAndScores } from '@/features/backtrack/calculate-all-scenarios-and-scores';
import { AuroriansModel } from '@/models/aurorians';
import { SolutionScenarioCard } from '@/components/SolutionScenarioCard';
import { ExcludeAurorianFormList } from '@/components/ExcludeAurorianFormList';
import type { SolutionScenario } from '@/features/backtrack/types';

interface CopilotSolutionProps {
  dataSource: CopilotType[];
  term: `${number}`;
}

interface QueryParams {
  k: number;
  box: 'whole' | 'my-box';
  disalbeAlternative?: boolean;
  enableExclude?: boolean;
  exclude?: {
    aurorianName: string;
    excludeBreakthrough?: number;
    excludeBreakthroughOnly?: boolean;
  }[];
  enableSaveLocalStorage?: boolean;
}

const initialValues = { k: 3, box: 'whole', exclude: [{}] as any[] } as const;

export const CopilotSolution: React.FC<CopilotSolutionProps> = ({
  dataSource,
  term,
}) => {
  const [{ WHOLE_BOX }] = useModel(AuroriansModel);

  const formRef = useRef<ProFormInstance>();
  const [current, setCurrent] = useState(1);
  const inViewRef = useRef<HTMLDivElement>(null);
  const LOCAL_STORAGE_SETTING_KEY = useMemo(
    () => `SINSA_DADDY_SOLUTIONS_FILTER_KEY_V1_${term}` as const,
    [term],
  );

  const [localSetting, setLocalSetting] = useLocalStorageState(
    LOCAL_STORAGE_SETTING_KEY,
    {
      defaultValue: undefined,
    },
  );

  const { data, loading, runAsync } = useRequest(
    async (params: QueryParams) => {
      let filterBox = WHOLE_BOX.aurorian_summaries;
      if (params.exclude) {
        filterBox = produce(WHOLE_BOX.aurorian_summaries, draft => {
          for (const aurorianInExclude of params.exclude!) {
            if (!aurorianInExclude.excludeBreakthroughOnly) {
              delete draft[aurorianInExclude.aurorianName];
            } else if (
              typeof aurorianInExclude.excludeBreakthrough === 'number' &&
              aurorianInExclude.excludeBreakthrough >= 1
            ) {
              draft[aurorianInExclude.aurorianName].breakthrough =
                aurorianInExclude.excludeBreakthrough - 1;
            }
          }
        });
      }

      const [solutionResult] = await Promise.all([
        Promise.resolve(
          calculateAllScenariosAndScores(
            {
              copilots: dataSource,
              availableBox: filterBox,
            },
            params.k,
            { disalbeAlternative: params.disalbeAlternative },
          ),
        ),
        new Promise<void>(resolve => window.setTimeout(resolve, 100)),
      ]);

      const rankSet = new WeakMap<SolutionScenario, number>();
      for (let i = 0; i < solutionResult.scenarios.length; i++) {
        const target = solutionResult.scenarios[i];
        rankSet.set(target, i);
      }

      gtag('event', 'get_solution_result', {
        k: params.k,
        box: params.box,
        disalbeAlternative: params.disalbeAlternative,
        enableExclude: params.enableExclude,
        solutions_length: solutionResult.scenarios.length,
        exclude: params.exclude
          ?.map(
            xclude =>
              `${xclude.aurorianName}_${
                (xclude.excludeBreakthroughOnly &&
                  xclude.excludeBreakthrough) ||
                ''
              }`,
          )
          .join(','),
      });

      return {
        solutionResult,
        rankSet,
      };
    },
    {
      manual: true,
      defaultParams: [initialValues],
    },
  );

  console.log('localSetting', localSetting);

  return (
    <>
      <Card style={{ marginBottom: '1rem' }}>
        <ProForm<QueryParams>
          formRef={formRef}
          onFinish={async params => {
            await runAsync(params);
            setCurrent(1);
          }}
          // sub="å¯»æ‰¾é˜Ÿä¼æ–¹æ¡ˆ"
          initialValues={localSetting ?? initialValues}
          layout="vertical"
          submitter={{
            // render: (_, dom) => <FooterToolbar>{dom}</FooterToolbar>,
            searchConfig: { submitText: 'å¯»æ‰¾åŒ¹é…æ–¹æ¡ˆ' },
            render(_, dom) {
              return (
                <Space wrap>
                  {dom}
                  {data?.solutionResult ? (
                    <Typography.Text>
                      å·²ä¸ºæ‚¨æ‰¾åˆ°{' '}
                      <Typography.Text strong>
                        {data?.solutionResult.scenarios.length}
                      </Typography.Text>{' '}
                      ä¸ªåŒ¹é…æ–¹æ¡ˆ
                    </Typography.Text>
                  ) : null}
                </Space>
              );
            },
          }}
          loading={loading}
        >
          <ProForm.Group>
            <ProFormRadio.Group
              name="k"
              label="é˜Ÿä¼æ•°é‡"
              rules={[{ required: true }]}
              options={[
                { label: 'ä¸¤é˜Ÿ', value: 2 },
                { label: 'ä¸‰é˜Ÿ', value: 3 },
                { label: 'å››é˜Ÿ', value: 4 },
              ]}
              allowClear={false}
              radioType="button"
            />

            <ProFormRadio.Group
              name="box"
              label="Box åŒ¹é…"
              options={[
                { label: 'å…¨å›¾é‰´æ»¡çªç ´ Box', value: 'whole' },
                { label: 'è‡ªå®šä¹‰ Box', value: 'my-box', disabled: true },
              ]}
              allowClear={false}
              radioType="button"
              rules={[{ required: true }]}
              extra="è‡ªå®šä¹‰ Box åŒ¹é…æš‚ä¸æ”¯æŒï¼Œæ— æ³•é€‰ä¸­ï¼Œæ•¬è¯·æœŸå¾…w"
            />
            <ProFormSwitch
              name={'disalbeAlternative'}
              label="ä¸è€ƒè™‘å¯æ›¿è‡ªç”±ä½"
              tooltip="å¼€å¯åï¼Œè®¡ç®—çš„é˜Ÿä¼æ–¹æ¡ˆä¸­ç»å¯¹ä¸ä¼šå‡ºç°é‡å¤å…‰çµï¼Œæ»¡è¶³å›¾é‰´å¤§ä½¬çš„å¼ºè¿«ç—‡"
            />
            <ProForm.Group>
              <ProFormSwitch
                name="enableExclude"
                label="é¢å¤–æ’é™¤å…‰çµ"
                tooltip="æ’é™¤å…‰çµä¼šå¿½ç•¥å¯æ›¿è‡ªç”±ä½"
              />
              <ProFormDependency name={['enableExclude']}>
                {({ enableExclude }) => {
                  if (enableExclude) {
                    return <ExcludeAurorianFormList name={'exclude'} />;
                  }
                  return null;
                }}
              </ProFormDependency>
            </ProForm.Group>
            <ProFormSwitch
              name={'enableSaveLocalStorage'}
              label="è®°ä½æˆ‘çš„è®¾ç½®"
              tooltip="å¼€å¯åï¼Œå½“å‰ç­›é€‰çš„è®¾ç½®å°†ä¼šå‚¨å­˜åœ¨æ‚¨çš„æµè§ˆå™¨æœ¬åœ°ï¼Œé¿å…ç¹çé‡å¤ç­›é€‰"
              fieldProps={{
                onChange(enableSaveLocalStorage) {
                  if (enableSaveLocalStorage) {
                    const currentFormValues = formRef.current?.getFieldsValue();
                    if (currentFormValues) {
                      setLocalSetting(currentFormValues);
                    }
                  } else {
                    setLocalSetting(undefined);
                  }
                },
              }}
            />
          </ProForm.Group>
        </ProForm>
      </Card>
      <div ref={inViewRef} />
      <List
        loading={loading}
        dataSource={data?.solutionResult?.scenarios}
        pagination={{
          align: 'center',
          defaultPageSize: 5,
          current,
          onChange(page) {
            setCurrent(page);
            window.scrollTo({
              top: inViewRef.current!.offsetTop,
              behavior: 'smooth',
            });
          },
          showSizeChanger: false,
        }}
        rowKey={sc => sc.copilots.map(c => c.bv).join('')}
        renderItem={item => {
          return (
            <Card
              title={
                <>
                  {data?.rankSet
                    ? `#${(data.rankSet.get(item) ?? 0) + 1} åŒ¹é…æ–¹æ¡ˆ `
                    : null}
                  {numeral(item.totalScore).format('0,0')}
                </>
              }
              key={item.copilots.map(c => c.bv).join('')}
              style={{ marginBottom: '1rem' }}
            >
              <SolutionScenarioCard solution={item} />
            </Card>
          );
        }}
      />
    </>
  );
};
